.subHeader-text {
  display: flex;
  /*center the text*/
  justify-content: center;
  padding: 60px;
  font-size: 48px;
  font-weight: 500;
  color: white;

  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
  /* background image is the backgroundSubheader img*/
  background:  linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(./backgroundSubHeader.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 10px;
  margin: 0 200px;
}
